import React, { useState } from 'react'
import { Alert } from 'react-bootstrap';
import Home from '../home/Home';
import Registration from '../Registration/Registration';
import {BrowserRouter as Router, Route, Link, NavLink, Switch} from "react-router-dom";

function Login() {

    const [emaillog, setEmaillog] = useState(" ");
    const [passwordlog, setPasswordlog] = useState(" ");

    const [flag, setFlag] = useState(false);

    const [home, setHome] = useState(true);


    function handleLogin(e) {
        e.preventDefault();
        let pass = localStorage.getItem('hardikSubmissionPassword').replace(/"/g, "");
        let mail = localStorage.getItem('hardikSubmissionEmail').replace(/"/g, "");
        // .replace(/"/g,"") is used to remove the double quotes for the string

        if (!emaillog || !passwordlog) {
            setFlag(true);
            console.log("EMPTY");
        } else if ((passwordlog !== pass) || (emaillog !== mail)) {
            setFlag(true);
        } else {
            setHome(!home);
            setFlag(false);
            alert("Login Successfully")
        }
    }
    
    function setClear(){
        setFlag(false);
    };


    return (
        <div>
            {home ? <form onSubmit={handleLogin}>
                <h3>LogIn</h3>
                <div className="form-group">
                    <label>Email</label>
                    <input type="text" className="form-control" placeholder="Enter email" required onChange={(event) => setEmaillog(event.target.value)} />
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password" className="form-control" placeholder="Enter password" required onChange={(event) => setPasswordlog(event.target.value)} /><br/>
                </div>

                <button type="submit" className="btn btn-dark btn-lg btn-block">Login</button>
                <input type="reset" onClick={setClear} className="btn btn-dark btn-lg btn-block"  style= {{float:"right"}}value="Cancel"></input>
              
                <p >
                <br/>
                    Don't have an account?<NavLink   to="/register"> Register</NavLink> 
                </p>

                {flag && <Alert color='primary' variant="warning" >
                    Incorrect Username or Password Please Try Again
                        </Alert>}
            </form>
                : <Login/>
                
            }

        </div>
    )
}

export default Login
